<template>

  <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
    <template v-slot:activator="{ on }">
      <v-btn color="blueButtons" dark v-on="on"> Cadastrar </v-btn>
    </template>

    <div class="background-dialog">
       <v-toolbar dark color="primary">
        <v-btn icon dark @click="dialog = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title> Cadastro de Servio </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn dark text @click="dialog = false">Salvar</v-btn>
        </v-toolbar-items>
      </v-toolbar>

      <v-container>
        <v-row
          align="center"
          justify="center"
        >
          <v-col
            cols="12"
            sm="10"
            md="10"
          >
            <v-card>
              <form class="pl-5 pr-5 pt-6 pb-6">
                <v-row>
                  <v-col sm="12">
                    <v-select
                      v-model="establishment"
                      required
                      :error-messages="productEstablishmentErrors"
                      @input="$v.establishment.$touch()"
                      @blur="$v.establishment.$touch()"
                      :items="establishments"
                      label="Estabelecimento"
                      single-line
                      item-text="name"
                      item-value="id"
                    />
                  </v-col>

                  <v-col sm="12">
                    <v-select
                      v-model="category"
                      required
                      :error-messages="productCategoryErrors"
                      @input="$v.category.$touch()"
                      @blur="$v.cateogry.$touch()"
                      :items="categories"
                      label="Categorias"
                      single-line
                      item-text="name"
                      item-value="id"
                      attach
                      chips
                      multiple
                      return-object
                    />
                  </v-col>

                  <v-col
                    cols="12"
                    sm="12"
                    md="6"
                  >
                    <v-text-field
                      v-model="product.title"
                      :error-messages="productNameErrors"
                      label="Nome do Servio"
                      required
                      @input="$v.product.title.$touch()"
                      @blur="$v.product.title.$touch()"
                    ></v-text-field>
                  </v-col>

                  <v-col
                    cols="12"
                    sm="12"
                    md="6"
                  >
                    <v-text-field
                      v-model="product.price"
                      :error-messages="productPriceErrors"
                      label="Preo"
                      required
                      @input="$v.product.price.$touch()"
                      @blur="$v.product.price.$touch()"
                    ></v-text-field>
                  </v-col>

                  <v-col
                    cols="12"
                    sm="12"
                    md="6"
                  >
                    <v-textarea
                      v-model="product.description"
                      :error-messages="productDescriptionErrors"
                      name="input-7-1"
                      label="Descrio do Servio"
                      rows="1"
                      required
                      @input="$v.product.description.$touch()"
                      @blur="$v.product.description.$touch()"
                    ></v-textarea>
                  </v-col>

                  <v-col
                    cols="12"
                    sm="12"
                    md="6"
                  >
                    <v-text-field
                      v-model="product.details.duraction"
                      :error-messages="productDuractionErrors"
                      label="Durao (minutos)"
                      required
                      @input="$v.product.details.duraction.$touch()"
                      @blur="$v.product.details.duraction.$touch()"
                    ></v-text-field>
                  </v-col>

                  <v-file-input
                    label="Avatar do Servio"
                    required
                    multiple
                    id="files"
                    ref="files"
                    v-model="files"
                  />
                </v-row>

                <v-row
                  align="center"
                  justify="center"
                >
                  <v-btn class="mr-4" @click="beforeSubmit" color="primary"> Cadastrar </v-btn>
                  <v-btn @click="clear" color="error"> Cancelar </v-btn>
                </v-row>

              </form>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </div>


  </v-dialog>
</template>

<script>
  import { validationMixin } from 'vuelidate'
  import { required, maxLength, email } from 'vuelidate/lib/validators'
  import { securedAxiosInstance } from '../plugins/axios'

  export default {
    mixins: [validationMixin],

    validations: {
      product: {
        title: { required },
        price: { required },
        description: { required },
        details: {
          duraction: { required }
         }
      },

      establishment: { required },
      category: { required }
    },

    data () {
      return {
        dialog: false,
        notifications: false,
        sound: true,
        widgets: false,

        product: {
          title: "",
          price: "",
          description: "",
          details: {
            duraction: "",
          },
        },

        establishment: null,
        establishments: [],
        categories: [],
        files: [],
        category: null
      }
    },

    mounted() {
      this.getEstablishments()
      this.getCategories()
    },

    computed: {

      productNameErrors() {
        const errors = []
        if (!this.$v.product.title.$dirty) return errors
        !this.$v.product.title.required && errors.push('Insira o nome do Servio.')
        return errors
      },

      productPriceErrors() {
        const errors = []
        if (!this.$v.product.price.$dirty) return errors
        !this.$v.product.price.required && errors.push('Insira o preo do Servio.')
        return errors
      },

      productDescriptionErrors() {
        const errors = []
        if (!this.$v.product.description.$dirty) return errors
        !this.$v.product.description.required && errors.push('Insira a descrio do Servio.')
        return errors
      },

      productDetailsValidateErrors() {
        const errors = []
        if (!this.$v.product.details.validate.$dirty) return errors
        !this.$v.product.details.validate.required && errors.push('Insira a validade do Servio.')
        return errors
      },

      productEstablishmentErrors() {
        const errors = []
        if (!this.$v.establishment.$dirty) return errors
        !this.$v.establishment.required && errors.push('Selecione um estabelecimento.')
        return errors
      },

      productDuractionErrors () {
        const errors = []
        if (!this.$v.product.details.duraction.$dirty) return errors
        !this.$v.product.details.duraction.required && errors.push('Insira o tempo de durao.')
        return errors
      },

      productCategoryErrors () {
        const errors = []
        if (!this.$v.category.$dirty) return errors
        !this.$v.category.required && errors.push('Selecione uma categoria.')
        return errors
      },
    },


    methods: {

      handleFilesUploads() {
        this.files = this.$refs.files.files;
      },

      beforeSubmit() {
        this.$v.$touch()

        if ( this.$v.$invalid) {
          return
        } else {
          this.submit()
        }
      },

      async submit() {
        let response = null
        let formData = new FormData();
        let category_id = []
        this.category.forEach( cat => {
          category_id.push(cat.id)
        })

        formData.append('category_id', JSON.stringify(category_id))
        formData.append('product', JSON.stringify(this.product))
        formData.append('establishment', JSON.stringify(this.establishment))
        formData.append('access_token', localStorage.getItem('access_token'))

        for( var i = 0; i < this.files.length; i++ ){
          let file = this.files[i];
          formData.append('images[' + i + ']', file);
        }

        await securedAxiosInstance.post('/api/v1/products', formData,
          { headers: {
              'Content-Type': 'multipart/form-data',
          } },
        )
        .then((result) => {
          response = result
        }).catch((err) => {
          response = err
        });

        if ( response.status == 201 ) {
          this.$v.$reset()
          this.product = {
            title: "",
            price: "",
            description: "",
            details: {
              validate: "",
            },
          },
          this.files = ''
          this.dialog = false
        }
      },

      async getEstablishments() {
        let response = null

        await securedAxiosInstance.get(`/api/v1/establishments`,
          {
            params: { access_token: localStorage.getItem('access_token') }
          }
        )
        .then((result) => {
          response = result
        }).catch((err) => {
          response = err
        });

        if ( response.status == 200 ) {
          this.establishments = response.data
        }
      },

      clear() {
        this.$v.$reset()
          this.product = {
            title: "",
            price: "",
            description: "",
            details: {
              validate: "",
            },
          },
          this.files = ''
          this.dialog = false
      },

      async getCategories() {
        let response = null
        await securedAxiosInstance.get(`/api/v1/private/category_types`,
          {
            params: { access_token: localStorage.getItem('access_token') }
          }
        )
        .then((result) => {
          response = result
        }).catch((err) => {
          response = err
        });

        if ( response.status == 200 ) {
          this.categories = response.data
        } else {
          this.error = true
        }
      },

    },

  }
</script>

<style scoped>
  .background-dialog {
    background-color:#fafafa;
    height: 100%;
  }

</style>

